import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SignupComponent } from './signup/signup.component';
import { SinginComponent } from './singin/singin.component';
import { ProfileComponent } from './profile/profile.component';
import { OrderComponent } from './order/order.component';
import { RouterTestingModule } from '@angular/router/testing';
import { RouteGuardService } from './services/route-guard.service';
import { RouteGuardAdminService } from './services/route-guard-admin.service';
import { MessageComponent } from './message/message.component';
import { LandingpageComponent } from './landingpage/landingpage.component';

const routes: Routes = [
  {path: 'dashboard', component: DashboardComponent, canActivate: [RouteGuardAdminService]},
  {path: 'signup', component: SignupComponent},
  {path: 'signin', component: SinginComponent},
  {path: 'profile', component: ProfileComponent , canActivate: [RouteGuardService]},
  {path: 'order', component: OrderComponent, canActivate: [RouteGuardService]},
  {path: 'message', component: MessageComponent, canActivate: [RouteGuardService]},
  {path: '', component: LandingpageComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
