import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IUser } from 'src/models/user';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(private httpClient: HttpClient) { }

  getUsers(pageSize: number, currentPage: number): Observable<IUser[]> {
    const query = `?pageSize=${pageSize}&currentPage=${currentPage}`;
    return this.httpClient.get<IUser[]>('api/users' + query);
  }
  getUsers2(pageSize: number, currentPage: number, filter: string): Observable<IUser[]> {
    const query = `?pageSize=${pageSize}&currentPage=${currentPage}&filter=${filter}`;
    return this.httpClient.get<IUser[]>('api/users' + query);
  }


  getUser(id: string): Observable<IUser> {
    return this.httpClient.get<IUser>('api/users/' + id);
  }

  deleteUser(id: string): Observable<IUser> {
    return this.httpClient.delete<IUser>('api/users/' + id);
  }
  createtUser(user: any): Observable<IUser> {
    return this.httpClient.post<IUser>('api/signup', user);
  }

  checkUser(password: string, username: string): Observable<any> {
    return this.httpClient.get<any>('api/user/' + username + '/' + password);
  }

  updateUser(id: string, data: any): Observable<any> {
    return this.httpClient.post('api/update/' + id, data);

  }

  updateUser2(id: string, data: any): Observable<any> {
    return this.httpClient.post('api/users/update/' + id, data);
  }
  isUserLoggedIn(): boolean {
    return sessionStorage.getItem('id') === null || sessionStorage.getItem('id') === '' ? false : true;
  }
  updateUserRole(id: string, role: string) {
    const userRole = {role};
    return this.httpClient.post('api/users/role/' + id , userRole);
  }
  isUserAdmin() {
    return sessionStorage.getItem('role') === 'admin' ? true : false;
  }
}
