import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ThrowStmt } from '@angular/compiler';

@Injectable({
  providedIn: 'root'
})
export class OrderService {
  deleteOrder(id: string): Observable<any> {
    return this.httpClient.delete('api/orders/' + id);
  }

  acceptOrder(id: string, data: any): Observable<any> {
    return this.httpClient.post('api/orders/accept', data);
  }

  constructor(private httpClient: HttpClient) { }


  getClientOders(id: string): Observable<any> {
    return this.httpClient.get('api/users/' + id + '/orders');

  }

  getAllOrders(): Observable<any> {
    return this.httpClient.get<any>('api/orders');
  }

  createOrder(userID: string, orderList: any): Observable<any> {
    return this.httpClient.post<any>('api/users/' + userID + '/order', orderList);
  }

}
