import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class MessageService {

  constructor(private http: HttpClient) { }

  createMessage(messageData: any): Observable<any> {
    return this.http.post('api/message', messageData);
  }

  getMessages(): Observable<any> {
    return this.http.get('api/messages');
  }

  deleteConvo(id: string): Observable<any> {
    return this.http.delete('api/messages/' + id);
  }
  replyMessage(msgID: string, message: string): Observable<any> {
    const msg = {message};
    return this.http.post('api/message/reply/' + msgID, msg);
  }
}
