import { Component, OnInit, ViewChild } from '@angular/core';
import { ProductService } from '../services/product.service';
import { OrderService } from '../services/order.service';
import { NgForm } from '@angular/forms';
import { MatPaginator } from '@angular/material';


@Component({
  selector: 'app-order',
  templateUrl: './order.component.html',
  styleUrls: ['./order.component.css']
})
export class OrderComponent implements OnInit {

  // create docTotal to store amout of documents
  docsTotal = 0;
  // for when the compnent is looading
  isLoading = false;

  products: any;
  productsList: Array<string> = [];
  productList2: any = [];
  total = 0;
  // view the orderd list
  viewList: boolean;
  // selected product quantity
  quantity = 0;

  // order message to indicate the success
  message = '';
  // use this to clear the order list and display the message
  isMessage = false;
  // use this to display error message color
  errorMessage = '';

  // create a viewChild to scout the dom

  constructor(private productService: ProductService, private orderService: OrderService) { }


  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  ngOnInit() {
    this.isLoading = true;

    this.paginator.page.subscribe(data => {
      this.productService.getProducts({page: ++data.pageIndex, perPage: data.pageSize, filter: ''}).subscribe( products => {
        console.log(data.pageIndex + ' pageIndex');
        this.products = products.docs;
        this.docsTotal = products.total;
      }, err => {
      });

      });


    this.getProducts();
    this.isLoading = false;
  }

  // get products on load
  getProducts() {
    this.productService.getProducts({page: 1, perPage: 5, filter : ''}).subscribe(products => {
      this.products  = products.docs;
      this.docsTotal = products.total;
    }, err => {
      console.log(err);
    });
  }

  // add items to list
  addTolist(form: NgForm, productID: string) {

    this.isMessage = false;
    let quantity = 1;
    quantity = form.value;

    const arr = [];



    this.products.forEach(element => {
        if (element._id.includes(productID) && this.productsList.indexOf(element._id) === -1) {
          console.log(element._id);
          if (!(this.productList2.includes(element)) && (this.productList2.indexOf(element) === -1)) {
            this.productsList.push(productID.trim());
            element.quantity = quantity;
            this.productList2.push(element);
          } else {

            element.quantity = 1 * ((element.quantity * 1) + (1 *  quantity));
          }
          this.viewList = true;
          this.total = 0;
          this.productList2.forEach(element => {
          this.total += (element.price * element.quantity);
          });
          // this.total = element.price * element.quantity;
      }
    });

    this.quantity = quantity;
  }

  // remove from list
  removeFromlist(form: NgForm, productID: string) {
   // tslint:disable-next-line: max-line-length
   this.productList2.forEach(element => {
    console.log(element._id);
    if (element._id === productID) {
         this.total = this.total - (element.price * element.quantity);
         if (this.total < 0) {
           this.total = 0;
         }
       }
     });

   const index: number = this.productsList.indexOf(productID);
   if (index !== -1) {
      this.productsList = this.productsList.slice(0, index).concat(this.productsList.slice(index + 1));
      this.productList2 = this.productList2.slice(0, index).concat(this.productList2.slice(index + 1));
    }
  }

  placeOrder() {
      const userID = sessionStorage.getItem('id');
      let list = '';
      let quantityList = '';
      this.productsList.forEach(element => {
        this.productList2.forEach(element2 => {
          if (element === element2._id) {
            list += element + '#';
            quantityList += element2.quantity + '#';
          }
        });
      });
      const totalprice = this.total;
      // // tslint:disable-next-line: object-literal-shorthand
      const data = {products: list.substr(0, list.length - 1), totalprice, quantitylist: quantityList.substr(0, quantityList.length - 1)};
      this.orderService.createOrder(userID, data).subscribe(respose => {
        if (respose) {
          this.errorMessage = 'alert alert-success';
          this.isMessage = true;
          this.message = 'Your Oder Was Successful! Please go to your email to See a summary of your Order :)';
        } else {
          this.isMessage = true;
          this.errorMessage = 'alert alert-warning';
          this.message = 'Something went wrong :(, try Signing in again';
        }
      }, err => {
        if (err) {
          this.isMessage = true;
          this.errorMessage = 'alert alert-warning';
          this.message = 'Something went wrong :(, try Signing in again';
        }
      });
  }

  // method to filter
  filterText(filterText: string) {
    this.isLoading = true;
    filterText = filterText.trim();
    this.paginator.pageIndex = 0;
    this.productService.getProducts(
      {page: ++this.paginator.pageIndex,
      perPage: this.paginator.pageSize,
      filter: filterText}).subscribe( products => {
      this.products = products.docs;
      this.docsTotal = products.total;
    }, err => {
    });
    this.isLoading = false;
  }

  placeOrderagain(list: any) {
    // const file = (event.target as HTMLInputElement).files[0];
    console.log(list);
  }

}
