import { Component, OnInit, ViewChild } from '@angular/core';
import { UserService } from '../services/user.service';
import { OrderService } from '../services/order.service';
import { PageEvent, MatPaginator } from '@angular/material';
import { NgForm } from '@angular/forms';
import { ProductService } from '../services/product.service';
import { MessageService } from '../services/message.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  constructor(private userService: UserService, private orderService: OrderService, private productService:
    // tslint:disable-next-line: align
    ProductService, private messageServive: MessageService) { }

    // viewChild to inspect element of the html. for events i guess

    @ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;


  Reject = 'Reject';

  Quantity: any;
  public Users = [];
  products: Array<any> = [];
  viewOrders = false;

  allOrders: any;
  oneOrder: any;

  // used for pagination
  recordsLength = 2;
  pageSizeOptions = [1, 3, 5, 8, 16, 40, 10000];
  usersPerPage = 5;
  currentPage = 0;

  // edit remove button
  btnEditRemove = 'Add Product';
  btnVisible = false;
  // what to display
  isViewAllOrders = false;
  isViewAllMessages = false;
  isViewAllUsers = false;

  isAddNewProduct = false;
  prod: any;
  isEditProduct = false;

  // editing the user role
  editRole = false;

  // storing messages
  messages: any;
  messageReply = '';
  isReply = false;
  isRead = false;
  infoMsg = '';
  userRole  = '';
    viewUserOrders(id: string) {
    this.viewOrders = true;
    this.isViewAllUsers = false;

    this.isAddNewProduct = false;

    this.orderService.getClientOders(id).subscribe(order => {
      this.oneOrder = order;
    }, errr => {
    });
  }

  deleteUser(id: string) {
    this.userService.deleteUser(id).subscribe(data => {
      this.getUsers();
    }, err => {
      console.log(err);
    });
  }

  onChangedPage(pageData: PageEvent) {

    this.currentPage = pageData.pageIndex + 1;
    this.usersPerPage =  pageData.pageSize;
    this.userService.getUsers(this.usersPerPage , this.currentPage);
    this.getUsers();
  }

  // all orders will be viewed
  viewAllOrders() {
    this.isViewAllOrders = true;
    this.isViewAllUsers = false;
    this.isViewAllMessages = false;
    this.viewOrders = false;

    this.isAddNewProduct = false;

    this.orderService.getAllOrders().subscribe(orders => {
      this.allOrders = orders;
      console.log(this.allOrders);
    }, err => {
      console.log(err);
    });
  }

  ngOnInit() {

    this.userRole = sessionStorage.getItem('role');
    this.isViewAllUsers = true;
    this.isViewAllOrders = false;
    this.isViewAllMessages = false;
    this.viewOrders = false;


    this.userService.getUsers(this.usersPerPage, 1).subscribe(users => {
      this.Users = users;
      this.recordsLength = this.Users.length;
    });
    this.getProducts();

  }



  getUsers() {
    this.ngOnInit();
    this.isViewAllOrders = false;
    this.isViewAllMessages = false;
    this.isViewAllUsers = true;
    this.isAddNewProduct = false;
  }


  // adding a product to database
  addProduct(ngform: NgForm) {
    if (ngform) {
      try {

      } catch (error) {
        console.log(error);
      }
    }
    this.isViewAllOrders = true;
    this.isViewAllUsers = false;
    this.isViewAllMessages = false;
    this.viewOrders = false;

    this.isAddNewProduct = true;

    if (ngform && !this.isEditProduct) {


      try {
        const product = {
          price : ngform.value.price,
          name : ngform.value.name
        };
        // now create the product
        this.productService.createProduct(product).subscribe(result => {

          this.getProducts();
        }, err => {
          console.log(err);
        });
      } catch (error) {
        console.log(error);
      }

    } else if (ngform && this.isEditProduct) {

      const product = {
        price : ngform.value.price,
        name : ngform.value.name
      };

      this.startEdit(product, this.prod._id);
      this.isEditProduct = false;
    }

  }
  addProduct2() {
  this.addProduct(null);
  }

  viewAllMessages() {
    this.isViewAllOrders = false;
    this.isViewAllUsers = false;
    this.isViewAllMessages = true;
    this.viewOrders = false;
    this.isAddNewProduct = false;
    this.isEditProduct = false;

    this.messageServive.getMessages().subscribe(messages => {
      this.messages = messages;
    });


  }
  replyMessage(userId: string) {
    this.messageServive.replyMessage(userId, this.messageReply).subscribe(msg => {
      if (msg) {
        this.infoMsg = 'A reply has been sent';
        this.isReply = true;
        this.viewAllMessages();
        setTimeout(() => {
          this.isReply = false;
          this.messageReply = '';
        }, 3000);
      }
    });
  }

  // delete unwanted messages, ridiculous that is. i value opinions and complains not stupidity
  deleteMessage(userId: string) {
    this.messageServive.deleteConvo(userId).subscribe(deletedMSG => {
      if (deletedMSG) {
        this.infoMsg = 'Conversation DELETED';
        this.isReply = true;
        this.viewAllMessages();
        setTimeout(() => {
          this.isReply = false;
          this.messageReply = '';
        }, 3000);
      }
    });
  }







  // to get all products
  getProducts() {
    this.productService.getProducts({page: 1, perPage: 10, filter: ''}).subscribe(products => {
      this.products = products.docs;

    }, err => {
      console.log(err);
    });

  }


  // FOR PRODUCTS
  deleteProduct(id: string) {
    this.productService.deleteProduct(id).subscribe(product => {

      this.getProducts();
    }, err => {
      console.log(err);
    });
  }
  // tslint:disable-next-line: member-ordering
  name;
  // tslint:disable-next-line: member-ordering
  price;
  editProduct(id: string, ngform: NgForm) {
    this.btnEditRemove = 'Edit Product';
    this.products.forEach(element => {
      if (element._id === id) {


        this.isEditProduct = true;
        this.name = element.name;
        this.price = element.price;
        this.prod = {
         _id: id,
         name : this.name,
         price : this.price
        };
      }
    });
  }
// h
  startEdit(prod: any, id: string) {
    this.productService.updateProduct(id , prod).subscribe(product => {
      if (product) {
        this.getProducts();
        this.price = '0';
        this.name = ' ';
        this.btnEditRemove = 'Add Product';
      }
    }, err => {
      console.log(err);
    });
  }
// delete order
  deleteOrder(id: string) {
    this.orderService.deleteOrder(id).subscribe(delOrder => {
      this.viewAllOrders();
    });
  }

// accept order
acceptOrder(id: string, userID: string) {
  const data = {userid: userID, orderid: id};
  this.orderService.acceptOrder(id, data).subscribe(acceptOrder => {
    this.viewAllOrders();
  });
}

  saveUserRole(id: string, form: NgForm) {
    this.userService.updateUserRole(id, form.value).subscribe(roleUpdate => {
      if (roleUpdate) {
        this.editRole = true;
        setTimeout(() => {
          this.editRole = false;
        }, 3000);
      }
    });
  }

// search user here
filterText(filterText: string) {

  filterText = filterText.trim();
  this.paginator.page.subscribe(data => {

  });
  this.paginator.pageIndex = 0;
  this.userService.getUsers2(this.paginator.pageSize, ++this.paginator.pageIndex, filterText).subscribe(data => {
    this.Users = data;
  }, err => {
    console.log(err);
  });
  // this.userService.getUsers()
}

}

