import { Component, OnInit } from '@angular/core';
import { MessageService } from '../services/message.service';

@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.css']
})
export class MessageComponent implements OnInit {

  message = '';
  subject = '';

  messages;
  userId = '';

  msgSuccess = false;
  isDeleted = false;
  constructor(private messageServive: MessageService) { }

  ngOnInit() {
    this.userId = sessionStorage.getItem('id');
    this.getMessages();
  }

  sendMessage() {

    const realMessage = {subject: this.subject, message : this.message, user: sessionStorage.getItem('id')};
    this.subject = '';
    this.message = '';
    this.messageServive.createMessage(realMessage).subscribe(mess => {
      if (mess) {
        this.msgSuccess = true;
      }
      this.ngOnInit();
      setTimeout(() => {
        this.msgSuccess = false;
      }, 3000);
    });
  }
  getMessages() {
    this.messageServive.getMessages().subscribe(messages => {
      this.messages = messages;
    });
  }
  getMyMessages() {
    this.ngOnInit();
  }

  deleteConvo(id: string) {
    this.messageServive.deleteConvo(id).subscribe(deleted => {
      if (deleted) {
        this.isDeleted = true;
      }
      this.ngOnInit();
      setTimeout(() => {
        this.isDeleted = false;
      }, 3000);
    });

  }

}
